import React from 'react'
import ReactDOM from 'react-dom'

class Popup extends React.Component {
    constructor(props) {
        super(props)
        this.state = {show: this.props.show, width: window.innerWidth, height: window.innerHeight}

        if (!this.state.show) {
            ReactDOM.unmountComponentAtNode(document.getElementById("bp-update-me"))
        }
    }

    updateDimensions = () => {
        this.setState({ width: window.innerWidth, height: window.innerHeight })
    }

    setCookie(cname, cvalue, exdays) {
        const d = new Date()
        d.setTime(d.getTime() + (exdays * 24 * 60 * 60 * 1000))
        let expires = "expires="+d.toUTCString()
        document.cookie = cname + "=" + cvalue + ";" + expires + ";path=/"
    }

    componentDidMount() {
        document.querySelector("body").classList.add("overflow-hidden")
        window.addEventListener('resize', this.updateDimensions)
    }

    componentDidUpdate() {
        if (!this.state.show) {
            ReactDOM.unmountComponentAtNode(document.getElementById("bp-update-me"))
        }
    }

    componentWillUnmount() {
        document.querySelector("body").classList.remove("overflow-hidden")
        window.removeEventListener('resize', this.updateDimensions)
    }

    render() {
        return (
            <div className="popup-overlay">
                <div className="popup-container">
                    <div className="popup-box">
                        <button className="popup-button" onClick={() => {
                            this.setCookie("bpp-667", "closed", 1)
                            this.setState({show: false})
                        }}>X</button>
                        <img className="popup-img" src={(this.state.width > 425) ? this.props.desktop : this.props.mobile} alt="baner spotkania" />
                    </div>
                </div>

            </div>
        )
    }
}

export default Popup