import React from 'react'
import ReactDOM from 'react-dom'
import Popup from './Popup'

import "./index.scss"

class BanerPopup extends React.Component {
    constructor(props) {
        super(props)
        this.state = { popupShow: (this.getCookie("bpp-667") != "closed") }
        
    }
      
    getCookie(cname) {
        let name = cname + "=";
        let ca = document.cookie.split(';');
        for(let i = 0; i < ca.length; i++) {
          let c = ca[i];
          while (c.charAt(0) == ' ') {
            c = c.substring(1);
          }
          if (c.indexOf(name) == 0) {
            return c.substring(name.length, c.length);
          }
        }
        return "";
    }
      

    render() {
        return (
            <Popup desktop={this.props.desktop} mobile={this.props.mobile} show={this.state.popupShow} />
        )
    }
}

const divsToUpdate = document.querySelectorAll(".bp-update-me")

divsToUpdate.forEach(function(div) {
    const data = JSON.parse(div.querySelector("pre").innerHTML)
    ReactDOM.render(<BanerPopup {...data} />, div)
})